/* ========== Modal ========== */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  opacity: 1;
  transition: opacity $transition, visibility $transition;

  &.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

.modal {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 280px;
  height: auto;
  padding: 48px 20px 40px;
  background-color: #ffffff;

  &__close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    background-image: url('../images/close.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-color: #ffffff;
    border: none;
    transition: fill $transition;
    cursor: pointer;
  }

  &__img-container {
    width: 240px;
    height: 356px;
    margin-bottom: 30px;
    border-radius: 5px;
    overflow: hidden;
    background-color: teal;
  }

  &__title {
    margin-bottom: 20px;

    @include font(500, 20px, 23px, $title-white-theme-color);
    text-transform: uppercase;
  }

  &__list {
    margin-bottom: 20px;
  }

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__subtitle {
    width: 70px;
    margin-right: 40px;
    white-space: nowrap;
    @include font(500, 12px, 16px, $subtitle-color);
  }

  &__number {
    @include font(500, 12px, 14px, $title-white-theme-color);

    &--orange {
      padding: 1px 8px;
      border-radius: 5px;
      background-color: $accent-color;
      color: $title-dark-theme-color;
    }

    &--gray {
      padding: 1px 8px;
      border-radius: 5px;
      background-color: #f7f7f7;
    }
  }

  &__modal-text {
    @include font(500, 12px, 16px, $title-white-theme-color);
  }

  &__description-title {
    margin-bottom: 10px;
    @include font(500, 12px, 16px, $title-white-theme-color);
  }

  &__description-text {
    margin-bottom: 20px;
    @include font(500, 12px, 20px, $title-white-theme-color);
  }

  &__button-container {
    display: flex;
  }

  &__button:first-child {
    margin-right: 15px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    display: flex;
    width: 618px;
    padding: 40px 29px 40px;

    &__img-container {
      display: flex;
      align-items: center;
      min-width: 264px;
      height: 373px;
      margin-bottom: 0px;
      margin-right: 30px;
    }

    &__content-container {
      width: 264px;
    }
  }
}

@media screen and (min-width: 1024px) {
  .modal {
    width: 882px;
    padding: 50px 30px 40px;

    &__img-container {
      min-width: 396px;
      height: 477px;
      margin-bottom: 30px;
      border-radius: 5px;
      overflow: hidden;
      background-color: teal;
    }

    &__title {
      font-size: 30px;
      line-height: 1.167em;
    }

    &__content-container {
      width: 396px;
    }
  }
}

/* ========== End Modal ========== */

